import React, { useEffect, useRef } from 'react';
import { useWindowSize } from '../../hooks/useWindowResize';
import _ from 'lodash';

import { DisplayMode } from '../../types/enums/displayMode';

import { IAlbum } from '../../types/interfaces/album';

import { Image } from '../Image';
import { Link } from 'react-router-dom';

import TranslationService from '../../services/TranslationService';
import UrlBuilderService from '../../services/UrlBuilderService';
import AlbumService from '../../services/AlbumService';

import "./AlbumGrid.scss";

export function AlbumsGrid(props: { displayMode?: DisplayMode; translations: any; profileId: string; pageSize?: number; }) 
{
    const namespaceClassName = "album";

    const [albums, setAlbums] = React.useState<IAlbum[]>([]);

    const [windowWidth, setWindowWidth] = React.useState<number>(0);
    const [itemsPerRowCount, setItemsPerRowCount] = React.useState<number>(0);
    
    const trxService = TranslationService();
	const urlBuilder = UrlBuilderService();
    const albumService = AlbumService();
    
    const loadInitialData = async () => 
    {
        setAlbums(await albumService.getAlbums(1, props.pageSize ?? 20));
    };

    useEffect(() => {
      loadInitialData();
    }, []);

    useEffect(() => {
		if(window.innerWidth >= 1140)
			setItemsPerRowCount(7);
		else if(window.innerWidth > 960)
			setItemsPerRowCount(5);
		else if(window.innerWidth >= 720)
			setItemsPerRowCount(3);
		else if(window.innerWidth >= 540)
			setItemsPerRowCount(2);			
		else
			setItemsPerRowCount(1);
			
	}, [windowWidth]);

	const [newWindowWidth] = useWindowSize();
	
	if(newWindowWidth !== windowWidth)
		setWindowWidth(newWindowWidth);

	const gridContainerRef = useRef<HTMLDivElement>(null);
	
	const dims = gridContainerRef?.current?.getBoundingClientRect();

	const percentagePx = (dims?.width ?? 600) / 100;

	const itemWidth = ((100 - itemsPerRowCount) / itemsPerRowCount);
	const itemHeight = (Math.floor(percentagePx * ((100 - itemsPerRowCount) / itemsPerRowCount)));

    const cssClasses: string[] = [namespaceClassName + "-grid"];

    if(props.displayMode === DisplayMode.Stencil)
    	cssClasses.push("skeleton");

    return (
      	<div ref={gridContainerRef} className={cssClasses.join(" ")}>
            {
				(albums ?? []).map((album: IAlbum) => (
					<span className={namespaceClassName + " rounded"} key={album.id} style={{width: itemWidth + "%" }}>
						<Link to={urlBuilder.albumUrl(album.id)} state={{backUrl: window.location.toString() }}>
							<span className={namespaceClassName + "-cover-image"}>
								<Image displayMode={props.displayMode} location={album.coverImageAddress ?? ""} height={itemHeight} />
							</span>
							<span className={namespaceClassName + "-details"}>
								<span className={namespaceClassName + "-details-name"}>{album.title}</span>
								<span className={namespaceClassName + "-details-itemcount"}>{album.itemCount} {trxService.trx(props.translations, album.itemCount > 1 ? "items" :  "item")}</span>
							</span>
						</Link>
					</span>
				))
            }
		</div>
    );
};