import React, { useEffect } from 'react';

import { DisplayMode } from '../../types/enums/displayMode';

import { IMediaItem } from '../../types/interfaces/mediaItem';

import { Link } from 'react-router-dom';
import { Image } from './../../components/Image';
import { Heading } from '../Heading';

import TranslationService from '../../services/TranslationService';
import UrlBuilderService from '../../services/UrlBuilderService';
import MediaItemService from '../../services/MediaItemService';

import "./ProfilePhotos.scss";

export function ProfilePhotos(props: { displayMode?: DisplayMode; translations: any; profileId: string; }) 
{
    const [photos, setPhotos] = React.useState<IMediaItem[]>([]);

    const trxService = TranslationService();
    const urlBuilder = UrlBuilderService();
    const mediaItemService = MediaItemService();
    
    const loadInitialData = async () => 
    {
        setPhotos((await mediaItemService.getMediaItems(1, 9)).slice(0, 9));
    };

    useEffect(() => {
		loadInitialData();
    }, []);

    const cssClasses: string[] = ["card", "profile-section", "rounded", "mt-3 mb-3"];

    if(props.displayMode === DisplayMode.Stencil)
		  cssClasses.push("skeleton");

	return (
		<div className={cssClasses.join(" ")}>
			<div className="profile-section-head">
				<Heading displayMode={props.displayMode} size="2" text={trxService.trx(props.translations, "heading_photos")} btnLinkUrl={urlBuilder.profileUrl("photos", props.profileId)} btnText={trxService.trx(props.translations, "heading_all_photos")}></Heading>
			</div>
			<div className="profile-section-body">
				<div className="photos-grid">
				{
					(photos).map((photo: IMediaItem) => (
					<span className="photo" key={photo.id}>
						<Link to={urlBuilder.photoUrl(photo.id)} state={{backUrl: window.location.toString() }}>
							<Image displayMode={props.displayMode} location={photo.address} width={150} height={150} />
						</Link>
					</span>
				))
				}
				</div>
			</div>
		</div>
	);
};