
import React, { useEffect } from 'react';
import _ from 'lodash';

import { DisplayMode } from '../../types/enums/displayMode';

import { IPeerInformation } from '../../types/objects/peerInformation';

import { Heading } from '../Heading';
import { ConnectPeerForm } from '../ConnectPeerForm';

import TranslationService from '../../services/TranslationService';
import UrlBuilderService from '../../services/UrlBuilderService';
import PeerService from '../../services/PeerService';

import "./ProfilePeers.scss";

export function ProfilePeers(props: { displayMode?: DisplayMode; translations: any; profileId: string; currentUserInformation: any; }) 
{
	const [peers, setPeers] = React.useState<IPeerInformation[]>([]);

	const trxService = TranslationService();
	const urlBuilder = UrlBuilderService();
	
	const peerService = PeerService();
	
	const loadInitialData = async () => 
	{	 
		await peerService.getPeers(1, 9).then((response: any) => 
		{
			if(response && _.isArray(response))
				setPeers(response);
		})
		.catch((error: Error) => {
		console.dir(error);
		});
	};

	useEffect(() => {
		loadInitialData();
	}, []);

	const getProfilePictureUrl = (peer: IPeerInformation) => 
	{		
		if(_.isEmpty(peer.profilePictureUrl))
		return "/images/placeholder-profile-picture.png";

		return peer.profilePictureUrl;
	};

	let counter = 0;

	const buildPeerItemClasses = () => 
	{
		counter++;
		
		if(counter > 3)
			counter = 1;

		const classes: string[] = ["peer"];

		if(counter === 1)
			classes.push("peer-s");
		
		if(counter === 3)
			classes.push("peer-e");

		return classes.join(" ");
	};

	const cssClasses: string[] = ["card", "profile-section", "rounded", "mb-3"];

    if(props.displayMode === DisplayMode.Stencil)
		  cssClasses.push("skeleton");

	return (
		<div className={cssClasses.join(" ")}>
			<div className="profile-section-head">
				<Heading displayMode={props.displayMode} size="2" text={trxService.trx(props.translations, "heading_peers")} btnLinkUrl={urlBuilder.profileUrl("peers", props.profileId)} btnText={trxService.trx(props.translations, "heading_all_peers")}></Heading>
			</div>
			<div className="profile-section-body">
				<div className="peers-grid">
				{
					(peers).map((peer: any, index) => (
						<div className={buildPeerItemClasses()} key={index}>
							<span className="profile-image"><img src={getProfilePictureUrl(peer)} alt={peer.firstName} /></span>
							<span className="name">{peer.firstName}</span>
							<span className="mutualpeers">{peer.mutalPeerCount} {trxService.trx(props.translations, peer.mutalPeerCount > 1 ? "mutual_peers" :	"mutual_peer")}</span>
						</div>
					))
				}
				</div>
				
				{
					(!peers || peers.length < 1) && 
					<ConnectPeerForm displayMode={props.displayMode} peerInformation={props.currentUserInformation} buttonLabel={trxService.trx(props.translations, "connect")} translations={props.translations} />
				}
			</div>
		</div>
	);
};