import _ from 'lodash';

import { MediaType } from '../types/enums/mediaType';

import UserSlugService from './UserSlugService';

export default function UrlBuilderService() 
{
    const userSlugService = UserSlugService();

    const getSlug = (text: string) =>
    {        
        if(!text || _.isEmpty(text?.trim()))
          return text;

        return text?.trim().replace(new RegExp(" ", "g"), "-").toLowerCase();
    }
    
    const settingsUrl = (categoryName?: string | undefined) =>
    { 
        const urlParts = ["settings"];

        if(categoryName && !_.isEmpty(categoryName?.trim()))
          urlParts.push(getSlug(categoryName));

        return "/" + urlParts.join("/");
    }
    
    const profileUrl = (sectionName?: string | undefined, profileId?: string | undefined, useSlug?: boolean | undefined) =>
    { 
        const urlParts = ["profile"];

        if(profileId)
        {
            //const userSlug = userSlugService.getSlug(profileId);
            
            /* userSlug ? urlParts.push(userSlug) : */ urlParts.push(profileId);
        }

        if(sectionName && !_.isEmpty(sectionName))
          urlParts.push(sectionName);

        return "/" + urlParts.join("/");
    }
    
    const mediaItemUrl = (mediaTypeId: MediaType, id?: string | undefined) =>
    { 
        const urlParts = [];

        if(mediaTypeId === MediaType.Image)
            urlParts.push("photo");

        if(mediaTypeId === MediaType.Video)
            urlParts.push("video");

        if(mediaTypeId === MediaType.Audio)
            urlParts.push("audio");

        if(id && !_.isEmpty(id?.trim()))
            urlParts.push(id);

        return "/" + urlParts.join("/");
    }
    
    const photoUrl = (id?: string | undefined) =>
    { 
        return mediaItemUrl(MediaType.Image, id);
    }
    
    const videoUrl = (id?: string | undefined) =>
    { 
        return mediaItemUrl(MediaType.Video, id);
    }
    
    const audioUrl = (id?: string | undefined) =>
    { 
        return mediaItemUrl(MediaType.Audio, id);
    }
    
    const albumUrl = (id?: string | undefined) =>
    { 
        const urlParts = ["album"];

        if(id && !_.isEmpty(id?.trim()))
            urlParts.push(id);

        return "/" + urlParts.join("/");
    }
    
    return {
      getSlug,
      settingsUrl,
      profileUrl,
      mediaItemUrl,
      photoUrl,
      videoUrl,
      audioUrl,
      albumUrl
    };
}