import React from 'react';
import { useLocation } from 'react-router-dom';

import _ from 'lodash';

import { PageReadyStatus } from '../../types/enums/pageReadyStatus';
import { DisplayMode } from '../../types/enums/displayMode';
import { MediaType } from '../../types/enums/mediaType';

import { IPeerInformation } from '../../types/objects/peerInformation';

import ApiService from '../../services/ApiService';
import ToastService from '../../services/ToastService';
import SettingsService from './../../services/SettingsService';
import TranslationService from './../../services/TranslationService';
import UserService from '../../services/UserService';

import { Header } from './../../components/Header';

import { ProfileHeader } from '../../components/ProfileHeader';
import { ProfileSummary } from '../../components/ProfileSummary';
import { ProfilePhotos } from '../../components/ProfilePhotos';
import { ProfilePeers } from '../../components/ProfilePeers';

import { PostEditor } from './../../components/PostEditor';
import { ProfilePosts } from '../../components/ProfilePosts';

import { ProfileAbout } from '../../components/ProfileAbout';

import { PeersList } from '../../components/PeersList';
import { PeerRequestsList } from '../../components/PeerRequestsList';

import { ProfileMedia } from '../../components/ProfileMedia';

import './ProfilePage.scss';

export function ProfilePage(props: {}) 
{	
	const [loadStatus, setLoadStatus] = React.useState<PageReadyStatus>(PageReadyStatus.Ready);
	const [preloadCount, setPreloadCount] = React.useState(0);

	const countPreloadCompletionAsync = () => setPreloadCount((preloadCount) => preloadCount + 1);

	const [translations, setTranslations] = React.useState<any>({ __loading: true });
	const [currentUserSettings, setCurrentUserSettings] = React.useState<any>({ __loading: true });
	const [currentUserProfile, setCurrentUserProfile] = React.useState<any>({ __loading: true });
	const [peersSample, setPeersSample] = React.useState<IPeerInformation[]>([]);

	const toastService = ToastService();
	const settingsService = SettingsService();
	const trxService = TranslationService();
	const userService = UserService();
	
	const hasInitialDataLoaded = () => 
	{
		return (loadStatus !== PageReadyStatus.Loaded && preloadCount === 3);
	}

	const loadInitialData = async () => 
	{
		setLoadStatus(PageReadyStatus.Loading);

		ApiService().canConnect().then(async () => 
		{
			await settingsService.getAll().then(async (settings: any | undefined) => 
			{
				if(settings)
					setCurrentUserSettings(settings);
					
				countPreloadCompletionAsync();
	
				await trxService.getTranslations(settingsService.findValue(settings, "Language Code", settingsService.defaults.languageCode)).then((trx: any | undefined) => 
				{
					setTranslations(trx);
					countPreloadCompletionAsync();
				})
				.catch((error: Error) => { console.log(error); });
				
				await userService.getProfile().then(async (userProfileData: any | undefined) => 
				{
					setCurrentUserProfile(() => userProfileData);
					countPreloadCompletionAsync();
				})
				.catch((error: Error) => { console.log(error); });
			})
			.catch((error: Error) => { 
				console.log(error); 
			});
		})
		.catch((error: Error) => 
		{
			// show warning
			console.log(error); 
			
			toastService.notify("Unable to connecto API.");
			
			setLoadStatus(PageReadyStatus.LoadError);
		});
	};

	const isPageReady = () => 
	{
		return loadStatus === PageReadyStatus.Loaded;
	}
	
	if(hasInitialDataLoaded()) { setLoadStatus(PageReadyStatus.Loaded); }
  
	if(loadStatus === PageReadyStatus.Ready)
	{
		loadInitialData();
	}

	const path = useLocation().pathname.split("/");

	let currentPage = "posts";
	let navCurrentPage = currentPage;

	if(path.length === 3 || path.length === 4)
	{
		currentPage = path[path.length-1];
		navCurrentPage = currentPage;

		if(navCurrentPage.indexOf("_") > -1)
			navCurrentPage = navCurrentPage.substring(0, navCurrentPage.indexOf("_"));
	}

	let profilePictureUrl = currentUserProfile?.profilePicture?.location ?? undefined;
	let coverPictureUrl = currentUserProfile?.coverPicture?.location ?? undefined;

	const onPostEditorPost = (data: any) => 
	{
		console.dir(data);
	}

	const getProfileMediaTabName = () => 
	{
		if(currentPage === "photos" || currentPage === "videos")
			return "items";
			
		if(currentPage === "photos_albums" || currentPage === "videos_albums")
			return "albums";
	};

	const getProfileMediaType = () => 
	{
		if(currentPage === "photos" || currentPage === "photos_albums")
			return MediaType.Image;
			
		if(currentPage === "videos" || currentPage === "videos_albums")
			return MediaType.Video;
	};

	const displayMode = !isPageReady() ? DisplayMode.Stencil : DisplayMode.Normal;

	return(
		<React.Fragment>
			<Header displayMode={displayMode} translations={translations} user={currentUserProfile} />
		
			<div className="container-fluid page-content">
				<div className="row g-0">

					<div className="main-content col-12 col-sm-12 col-md-12 col-lg-10 col-xl-8 offset-lg-1 offset-xl-2">
						
					<div className="row">
						<div className="col-12">
						<ProfileHeader profileId={currentUserProfile.id} profileUserFullName={currentUserProfile.fullName} peersSample={peersSample} profilePictureUrl={profilePictureUrl} coverPictureUrl={coverPictureUrl} currentPageName={navCurrentPage} translations={translations} displayMode={displayMode} />
						</div>
					</div>
						
					{ currentPage === "posts" &&
						<div className="row">
							<div className="col-12 col-sm-5 col-md-4 col-lg-5 col-xl-5">
							<ProfileSummary profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} />
							<ProfilePhotos profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} />
							<ProfilePeers profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} currentUserInformation={currentUserProfile} />
							</div>
							
							<div className="col-12 col-sm-7 col-md-8 col-lg-7 col-xl-7">
							<main>
								<PostEditor userInfo={currentUserProfile} displayMode={displayMode} translations={translations} onPost={onPostEditorPost} />
								
								<ProfilePosts userInfo={currentUserProfile} displayMode={displayMode} translations={translations} />
							</main>
							</div>
						</div>
					}
						
					{ currentPage !== "posts" &&
						<div className="row">
							<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
							<main>
								{ currentPage === "about" && <ProfileAbout profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} /> }
								{ currentPage === "peers" && <PeersList profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} pageSize={100} /> }
								{ currentPage === "peers-requests" && <PeerRequestsList profileId={currentUserProfile.id} displayMode={displayMode} translations={translations} pageSize={100} /> }
								{ (currentPage === "photos" || currentPage === "photos_albums" || currentPage === "videos" || currentPage === "videos_albums") && 
								<ProfileMedia profileId={currentUserProfile.id} tab={getProfileMediaTabName()} mediaType={getProfileMediaType()} displayMode={displayMode} translations={translations} pageSize={100} /> 
								}							
							</main>
							</div>
						</div>
					}

					</div>

				</div>
			</div>
		</React.Fragment>
	);
}