import React, { useState, useEffect, ChangeEvent } from "react";

import { DisplayMode } from "../../types/enums/displayMode";

import { IUserInfo } from "../../types/interfaces/user";
import { IProfilePostItem } from "./../../types/api/api";

import PostService from "../../services/PostService";

import { ProfilePostContentItemComponent } from "./../../components/ProfilePostContentItem";

import "./ProfilePosts.scss";

export interface IProfilePostsState {
    userInfo: IUserInfo; 
}
  
export function ProfilePosts(props: { displayMode?: DisplayMode; translations: any; userInfo: IUserInfo; }) 
{
    // TODO: Remove
    const [loadingStatus, setLoadingStatus] = useState<string>("loading");
    const [posts, setProfilePostsItems] = useState<IProfilePostItem[]>([]);
    const [currentProfilePostsItem, setCurrentProfilePostsItem] = useState<IProfilePostItem | null>(null);
    const [currentIndex, setCurrentIndex] = useState<number>(-1);

    const postService = PostService();
    
    useEffect(() => {
        retrieveProfilePostsItems();
    }, []);

    const retrieveProfilePostsItems = async () => 
    {        
        await postService.getPosts(1, 10).then((response: any) => 
        {
            if(response)
                setProfilePostsItems(response ?? []);

            setLoadingStatus("loaded");
        })
        .catch((error: Error) => {
            setLoadingStatus("loaded");
        });
    };

    const refreshList = () => {
        retrieveProfilePostsItems();
        setCurrentProfilePostsItem(null);
        setCurrentIndex(-1);
    };

    return (
        <ul className="feed">
            {posts && posts.map((feedItem, index) => (
                <ProfilePostContentItemComponent key={index} data={feedItem} getDataUrl={"/data/post"} createdByUser={props.userInfo} translations={props.translations} displayMode={props.displayMode} />
            ))}
        </ul>
    );
}