import React, { useEffect } from 'react';
import _ from 'lodash';

import { DisplayMode } from '../../types/enums/displayMode';

import { Link } from 'react-router-dom';
import { AlbumsGrid } from '../AlbumGrid';
import { MediaItemsGrid } from '../MediaItemGrid';

import TranslationService from '../../services/TranslationService';
import UrlBuilderService from '../../services/UrlBuilderService';

import "./ProfileMedia.scss";
import { MediaType } from '../../types/enums/mediaType';

interface ITab
{
	id: string;
	titleTrxCode: string;
	url: string;
}

export function ProfileMedia(props: {  tab?: "items" | "albums";  mediaType?: MediaType; displayMode?: DisplayMode; translations: any; profileId: string; pageSize?: number; }) 
{
    const trxService = TranslationService();
    const urlBuilder = UrlBuilderService();
    
    const loadInitialData = async () => 
    {
        //setPhotos(await photoService.getPeers(1, props.pageSize ?? 20));
    };

    useEffect(() => {
      loadInitialData();
    }, []);

	let heading = "";	
	const tabs: ITab[] = [];

	if(props.mediaType === MediaType.Video)
	{
		heading = trxService.trx(props.translations, "heading_videos");
		
		tabs.push({ id: "items", titleTrxCode: "your_videos", url: urlBuilder.profileUrl("videos", props.profileId) });
		tabs.push({ id: "albums", titleTrxCode: "your_albums", url: urlBuilder.profileUrl("videos_albums", props.profileId) });
	}
	else
	{
		heading = trxService.trx(props.translations, "heading_photos");

		tabs.push({ id: "items", titleTrxCode: "your_photos", url: urlBuilder.profileUrl("photos", props.profileId) });
		tabs.push({ id: "albums", titleTrxCode: "your_albums", url: urlBuilder.profileUrl("photos_albums", props.profileId) });
	}

    const cssClasses: string[] = ["card", "photo-list", "rounded", "mt-3 mb-3"];

    if(props.displayMode === DisplayMode.Stencil)
      	cssClasses.push("skeleton");

    return (
      <div className={cssClasses.join(" ")}>
          <div className="photo-list-head">
            <h2>
              {heading}
              <span className="btn btn-sm btn-link float-end">{trxService.trx(props.translations, "add_photos_slash_videos")}</span>
            </h2>
          </div>
          <div className="photo-list-body">
			<ul className="photo-list-body-tabs nav nav-tabs">
			{(tabs).map((tab: ITab, index: number) => (
				<li className="nav-item" key={index}>
					<Link className={"nav-link" + (props.tab === tab.id ? " active" : "")} to={tab.url}>{trxService.trx(props.translations, tab.titleTrxCode)}</Link>
				</li>
			))}
			</ul>

			<div className="photo-list-body-content">
			{ props.tab === "items" && <MediaItemsGrid profileId={props.profileId} displayMode={props.displayMode} translations={props.translations} /> }
			{ props.tab === "albums" && <AlbumsGrid profileId={props.profileId} displayMode={props.displayMode} translations={props.translations} /> }
			</div>
      </div>
      </div>
    );
};