import React from 'react';
import _ from 'lodash';

import { DisplayMode } from '../../types/enums/displayMode';

import { Link } from 'react-router-dom';

import "./Heading.scss";

export function Heading(props: { displayMode?: DisplayMode; text: string; size: "1" | "2" | "3" | "4" | "5" | "6"; btnLinkUrl?: string; btnText?: string; }) 
{
    const wrapHeaderTag = (size: "1" | "2" | "3" | "4" | "5" | "6", content: any) =>
    {
        const className = (props.displayMode === DisplayMode.Stencil) ? "text-placeholder" : "";
    
        switch(size)
        {
            case "1": 
                return <h1 className={className}>{content}</h1>;
                
            case "2": 
                return <h2 className={className}>{content}</h2>;
            
            case "3": 
                return <h3 className={className}>{content}</h3>;
                
            case "4": 
                return <h4 className={className}>{content}</h4>;
            
            case "5": 
                return <h5 className={className}>{content}</h5>;
                
            case "6": 
                return <h6 className={className}>{content}</h6>;
        }
    };
    
    if(props.displayMode === DisplayMode.Stencil)
        return (
            <React.Fragment>
                {wrapHeaderTag(props.size, 
                    <React.Fragment>
                        <span style={{width:200}}>&nbsp;</span>
                        { (props.btnLinkUrl && !_.isEmpty(props.btnLinkUrl)) && (props.btnText && !_.isEmpty(props.btnText)) && 
                            <div className="btn btn-sm btn-placeholder btn-link float-end">&nbsp;</div> 
                        }
                    </React.Fragment>
                )}
            </React.Fragment>
        );

    return (
        <React.Fragment>
            {wrapHeaderTag(props.size, 
                <React.Fragment>
                    { props.text }
                    { (props.btnLinkUrl && !_.isEmpty(props.btnLinkUrl)) && (props.btnText && !_.isEmpty(props.btnText)) && 
                        <Link className="btn btn-sm btn-link float-end" to={props.btnLinkUrl}>{props.btnText}</Link> 
                    }
                </React.Fragment>
            )}
        </React.Fragment>
    );
};