import _ from "lodash";

import { DisplayMode } from '../../types/enums/displayMode';

import { Icon } from "../Icon";

import "./Button.scss";

interface IButtonProps 
{
	displayMode?: DisplayMode;
	primary?: boolean;
	size?: "small" | "medium" | "large";
	label?: string;
	iconName?: string;
	onClick?: () => void;
	cssClassNames?: string;
}

export const Button = ({ displayMode, label, iconName,  primary = false, size = "small", onClick, cssClassNames }: IButtonProps) => 
{
	const handleClick = () => 
	{ 
		if(onClick && typeof(onClick) === "function")
			onClick();
	};

	const cssClasses: string[] = ["btn"];

	if(size === "small")
		cssClasses.push("btn-sm");
	
	if(size === "large")
		cssClasses.push("btn-lg");

	if(displayMode === DisplayMode.Stencil)
	{
		cssClasses.push("btn-placeholder");
	}
	else
	{
		if(cssClassNames && !_.isEmpty(cssClassNames))
			cssClasses.push(cssClassNames);
	}

	const defaultLabel = !iconName || _.isEmpty(iconName) ? "Button" : "";

	return (
		<button type="button" className={cssClasses.join(" ")} onClick={handleClick}>
			{ iconName && !_.isEmpty(iconName) && <Icon name={iconName} /> }
			{ !label && _.isEmpty(label) ? defaultLabel : label }
		</button>
	);
};
