import React, { useState, useEffect } from "react";

import { DisplayMode } from "../../types/enums/displayMode";

import { IUserInfo } from "../../types/interfaces/user";
import { IFeedItem } from "./../../types/api/api";

import ApiService from "./../../services/ApiService";

import { FeedContentItemComponent } from "./../../components/FeedContentItem";

import "./Feed.scss";

export function Feed(props: { 
    displayMode?: DisplayMode;
    translations: any
}) 
{
    // TODO: Remove
    const [loadingStatus, setLoadingStatus] = useState<string>("loading");
    const [feedItems, setFeedItems] = useState<Array<IFeedItem>>([]);
    const [currentFeedItem, setCurrentFeedItem] = useState<IFeedItem | null>(null);
    const [currentIndex, setCurrentIndex] = useState<number>(-1);

    useEffect(() => {
        retrieveFeedItems();
    }, []);

    const retrieveFeedItems = async () => {
        
        setTimeout(async function()
        {
            await ApiService().getAll("feed?page=1&pageSize=10")
                .then((response: any) => 
                {
                    setFeedItems(response.data);
                    setLoadingStatus("loaded");
                })
                .catch((err: Error) => {
                    console.log(err);
                    setLoadingStatus("loaded");
                });
        }, 2000);
    };

    const refreshList = () => {
        retrieveFeedItems();
        setCurrentFeedItem(null);
        setCurrentIndex(-1);
    };

    const createdByUser:IUserInfo = {
        id: "12345",
        firstName: "Darin",
        fullName: "Darin Morris",
        gender: 1,
        profilePicture: { location:"/images/temp/profile-photo.jpg" },
        coverPicture: { location:"/images/temp/cover-photo.jpg" }, 
        description: "Aliquam at tellus pellentesque nunc porta sollicitudin. Suspendisse et purus et metus sagittis rutrum.",
        websiteUrl: "https://www.darinm.com"
    };

    return (
        <React.Fragment>
        {props.displayMode === DisplayMode.Normal &&
            <ul className="feed">
                {(feedItems).map((feedItem, index) => (
                    <FeedContentItemComponent key={`f_${index}`} translations={props.translations} data={feedItem} getDataUrl={"/data/post"} createdByUser={createdByUser} />
                ))}
            </ul>
        }
        </React.Fragment>
    );
}