import _ from 'lodash';

import { IApiResponse } from '../types/api/apiResponse';
import { IPost } from '../types/interfaces/post';

import ApiService from "./ApiService";

export default function PostService() 
{
    async function getPost(postId: string) 
    {
        if(_.isEmpty(postId))
            return Promise.reject(new Error("postId is invalid."));

        const endpoint = `posts/${postId}`;

        return ApiService().get(endpoint).then((response: IApiResponse<IPost> | undefined) =>
        {
            const data:IPost | null = ApiService().getDataPayload(response);

            if(!data)
                return Promise.reject(ApiService().buildNoPayloadError("post"));

            return Promise.resolve<IPost>(data);

        }).catch((error: Error) => 
        {
            return Promise.reject(error);
        });
    }

    async function getPosts(page: number, pageSize: number) 
    {
        const endpoint = "posts";

        const parameters = ApiService().buildParameters([
            { key: "page", value: page },
            { key: "pageSize", value: pageSize }
          ]);
  
        return ApiService().get(endpoint, undefined, parameters).then((response: IApiResponse<IPost[]> | undefined) =>
        {
            const data:IPost[] | null = ApiService().getDataPayload(response);

            if(!data)
                return Promise.reject(ApiService().buildNoPayloadError("post"));

            return Promise.resolve(data);

        }).catch((error: Error) => 
        {
            return Promise.reject(error);
        });
    }
    
    async function createPost(text: string, attachments: any[]) 
    {
        const endpoint = "posts";

        const payload = [{
            "text": text,
            "attachments": attachments
        }];

        return ApiService().put(endpoint, payload).then((response: IApiResponse<IPost[]> | undefined) =>
        {
            const data:IPost[] | null = ApiService().getDataPayload(response);

            if(!data)
                return Promise.reject(ApiService().buildNoPayloadError("post"));

            return Promise.resolve(data);

        }).catch((error: Error) => 
        {
            return Promise.reject(error);
        });
    }
    
    return {
        getPost,
        getPosts,
        createPost
    };
}