import _ from 'lodash';

import { IQueryParameter } from '../types/api/api';

import ApiService from "./ApiService";

export default function AlbumService() 
{
    async function getAlbum(albumId: string) 
    {
        if(_.isEmpty(albumId))
            return Promise.reject(new Error("albumId is invalid."));

        const endpoint = `albums/${albumId}`;

        return ApiService().get(endpoint).then((response: any | undefined) =>
        {
            return Promise.resolve((response && _.isObject(response.data)) ? response.data : {});

        }).catch((error: Error) => 
        {
            return Promise.reject(error);
        });
    }

    async function getAlbums(page: number, pageSize: number) 
    {
        const endpoint = "albums";

        const parameters: IQueryParameter[] = [
            { key: "page", value: page },
            { key: "pageSize", value: pageSize }
          ];
  
        return ApiService().get(endpoint, undefined, parameters).then((response: any | undefined) =>
        {
            return Promise.resolve((response && _.isObject(response.data)) ? response.data : {});

        }).catch((error: Error) => 
        {
            return Promise.reject(error);
        });
    }
    
    return {
        getAlbum,
        getAlbums
    };
}