import React from "react";
import _ from 'lodash';
import moment from 'moment';

import { DisplayMode } from "../../types/enums/displayMode";

import { IUserInfo } from '../../types/interfaces/user';
import { IImage } from "./../../types/objects/image";
import { IFeedItem } from "../../types/api/api";
import { IAlbumItem } from '../../types/interfaces/albumItem';

import TextService from "./../../services/TextService";
import TranslationService from "../../services/TranslationService";
import UrlBuilderService from "../../services/UrlBuilderService";

import { ProfileImage } from './../../components/ProfileImage';
import { Icon } from './../../components/Icon';
import { ImageSet } from './../../components/ImageSet';
import { AlbumCover } from "../AlbumCover";

import "./ProfilePostContentItem.scss";
import { PostLinkAttachment } from "../PostLinkAttachment";
import { ILink } from "../../types/interfaces/link";
import { IPostAttachmentType } from "../../types/interfaces/postAttachmentType";
import { AttachmentType } from "../../types/enums/attachmentType";
import { IAlbum } from "../../types/interfaces/album";

export function ProfilePostContentItemComponent(props: {
    data?: IFeedItem;
    getDataUrl?: string | null | undefined;
    createdByUser: IUserInfo;
    mediaItems?: IAlbumItem[];
    hardBorders?: boolean;
    displayMode?: DisplayMode;
    translations: any;
}) 
{
    const [state, setState] = React.useState({ 
        data: props.data,
        getDataUrl: props.getDataUrl ?? null,
        mediaItems: props.mediaItems ?? [], 
        hardBorders: props.hardBorders ?? false
    });

    const trxService = TranslationService();
    const urlBuilder = UrlBuilderService();
    
    const profileUrl = urlBuilder.profileUrl(undefined, props.createdByUser.id);
    const profileFullName = props.createdByUser?.fullName;
    const profileGender = props.createdByUser?.gender ?? undefined;
    const profilePictureUrl = props.createdByUser?.profilePicture?.location ?? "";

    const getLinkAttachment = () => 
    {
        if(!props.data || !props.data.attachments || props.data.attachments.length < 1)
            return undefined;
        
        if(!props.data.attachments[0] || !props.data.attachments[0].content)
            return undefined;

        const content: ILink = props.data.attachments[0].content;
        return content;
    };

    const getAlbumAttachment = () => 
    {
        if(!props.data || !props.data.attachments || props.data.attachments.length < 1)
            return undefined;
    
        if(!props.data.attachments[0] || !props.data.attachments[0].content)
            return undefined;

        const content: IAlbum = props.data.attachments[0].content;
        return content;
    };

    const roundingClasses = state.hardBorders ? "rounded-0" : "rounded";
    const cssClasses: string[] = ["feed-content-item", roundingClasses, "mt-3 mb-4"];

    const linkAttachment = getLinkAttachment();
    const albumAttachment = getAlbumAttachment();

    console.dir(props.data);
    
    return (
        <div className={cssClasses.join(" ")}>
            <div className={"card " + roundingClasses + " feed-content-item-content"}>

                <div className="feed-content-item-header">
                    <div className="profile-picture rounded">
                        {(state.data === null || props.displayMode === DisplayMode.Stencil ? <span className="skeleton" style={{width:"40px", height:"40px"}}></span> :                         
                            <><ProfileImage translations={props.translations} peerName={profileFullName} imageAddress={profilePictureUrl} profileUrl={profileUrl} height={40} gender={profileGender} /></>
                        )}
                    </div>
                
                    <div className="feed-content-item-details">
                        <h4>{(state.data === null || props.displayMode === DisplayMode.Stencil ? <span className="skeleton w40"></span> : <a href={profileUrl} role="link" tabIndex={0}><span>{props.createdByUser.fullName}</span></a>)}</h4>
                        <span className="txt-sm"><span className="timestamp">
                        {(state.data === null || props.displayMode === DisplayMode.Stencil ? <span className="skeleton w25"></span> : <a href={`/post/${state.data!.id}`} role="link" tabIndex={0}><span title={moment(state.data?.createdTimestamp).format('LLLL')}>{moment(state.data?.createdTimestamp).fromNow()}</span></a>)}
                        </span></span>
                    </div>

                    <div className="actions">
                        <div aria-expanded="false" aria-haspopup="menu" aria-label="Actions for this post" role="button">
                            <div>...</div>
                        </div>
                    </div>
                </div>

                <div className="card-body">
                    {(state.data === null || props.displayMode === DisplayMode.Stencil ?
                        <div className="placeholder-body">
                            <p>
                                <span className="skeleton x2 w100"></span>
                                <span className="skeleton x2 w15"></span>
                                <span className="skeleton x2 w80"></span>
                                <span className="skeleton x2 w60"></span>
                                <span className="skeleton x2 w40"></span>
                                <span className="skeleton x2 w25"></span>
                                <span className="skeleton x2 w50"></span>
                                <span className="skeleton x2 w15"></span>
                                <span className="skeleton x2 w95"></span>
                            </p>
                        </div> :
                        <div className="card-text" dangerouslySetInnerHTML={{__html: TextService().formatHtml(state.data!.text) }}></div>
                    )}
                </div>

                {props.data && props.data.attachments && 
                <div className={"feed-content-item-attachment"}>
                    {linkAttachment &&  
                        <PostLinkAttachment title={linkAttachment.title} url={linkAttachment.url} description={linkAttachment.description} imageUrl={linkAttachment.previewImage ? linkAttachment.previewImage.location : undefined} />
                    }
                    {albumAttachment && 
                        <AlbumCover mediaItems={state.mediaItems} maxWidth={100} maxWidthUnit={"%"} maxHeight={450} />
                    }
                </div>}
                
                {(state.data !== null ?
                <div className="feed-content-item-interactions">
                    <div className="interactions-details">
                        <div className="count reactions-count">
                            <a href="#" className="fw-n">{0}</a>
                        </div>
                        <div className="count comments-count">
                            <a href="#" className="fw-n">{0} {trxService.trx(props.translations, "comments")}</a>
                        </div>
                        <div className="count shares-count">
                            <a href="#" className="fw-n">{0} {trxService.trx(props.translations, "shares")}</a>
                        </div>
                    </div>
                    
                    <div className="actions">
                        <span className="action react">
                            <span><Icon name="thumb_up" marginEnd={2} /> {trxService.trx(props.translations, "like")}</span>
                        </span>

                        <span className="action comment">
                            <span><Icon name="insert_comment" marginEnd={2} /> {trxService.trx(props.translations, "comment")}</span>
                        </span>

                        <span className="action share">
                            <span><Icon name="share" marginEnd={2} /> {trxService.trx(props.translations, "share")}</span>
                        </span>
                    </div>
                </div>: null)}
            </div>
        </div>
    );
}